#addUserIconContainer {
    width: 40px;
    height: 40px;
    margin: 15px;
    margin-top: 12px;
    margin-bottom: 0px;
    border-color: #C6C6C6;
    border-style: dashed;
    border-width: 1px;
    stroke: #C6C6C6;
    stroke-width: 3px;
    border-radius: 30px;
}

@-webkit-keyframes newProjectAnimationDarken {
    from      {border-color: #C6C6C6;
            stroke: #C6C6C6}
    to      {border-color: grey;
            stroke: grey}
}

@keyframes newProjectAnimationDarken {
    from      {border-color: #C6C6C6;
            stroke: #C6C6C6}
    to      {border-color: grey;
            stroke: grey}
}

#addUserIconContainer:hover {
    -webkit-animation-name: newProjectAnimationDarken;
            animation-name: newProjectAnimationDarken;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
    border-color: grey;
    cursor: pointer;
    stroke: grey;
}

@-webkit-keyframes projectIndicatorEnter {
    from    {margin-left: -70px}
    to      {margin-left: -60px}
}

@keyframes projectIndicatorEnter {
    from    {margin-left: -70px}
    to      {margin-left: -60px}
}

#projectIndicator {
    width: 50px;
    height: 50px;
    margin-left: -60px;
    margin-top: 5px;
    border-radius: 10px;
    background-color: grey;
    -webkit-animation-name: projectIndicatorEnter;
            animation-name: projectIndicatorEnter;
    -webkit-animation-duration: 0.3s;
            animation-duration: 0.3s;
}

.newProjectPicture {
    width: 40px;
    margin-top: 7px;
    margin-left: 0px;
    height: auto;
}

@-webkit-keyframes newProjectAnimationDarkenPlus {
    from      {stroke: #C6C6C6}
    to      {stroke: grey}
}

@keyframes newProjectAnimationDarkenPlus {
    from      {stroke: #C6C6C6}
    to      {stroke: grey}
}

