#projectTitleContainer {
    width: 100%;
    height: 41px;
    margin: 0px;
    margin-bottom: 0px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    top: 0px;
    /* position: fixed; */
}

.profileImageSelect {
    border-radius: 50%;
    width: 50px;
    height: 50px;
}

#projectTitle {
    color: white;
    left: 12px;
    margin: auto;
    top: 10px;
}

#projectDescription {
    color: white;
    left: 13px;
    position: absolute;
    top: 27px;
    font-weight: lighter;
}

#projectSettingsIcon {
     height: 27px;
     margin-top: 7px;
     margin-right: 7px;
}

 #projectSettingsIcon:hover {
     cursor: pointer;
}

.submitFinalButton {
    border-color: #4A4A4A;
    background-color: transparent;
    width: 130px;
    border-radius: 20px;
    color: #4A4A4A;
    margin-left: 15px;
}

.submitFinalButton:hover {
    margin-top: -1px;
}

.settingsPopupOverlay {
    background-color: transparent !important;
    /* background: rgba(186, 191, 190, 0.6) !important; */
}

.settingsPopupModal {
    position: absolute !important;
    top: 41px !important;
    right: 350px !important;
    border-radius: 5px;
    border: 0px solid grey;
}

.assignUserOverlay {
    /* background-color: lightgrey !important; */
    background: rgba(186, 191, 190, 0.6) !important;
}

.copyProjectModal {
    width: 500px;
    border-radius: 5px;
    border: 1px solid grey;
}

.secSettingsPopupOverlay {
    background-color: transparent !important;
    /* background: rgba(186, 191, 190, 0.6) !important; */
}

.secSettingsPopupModal {
    position: absolute !important;
    top: 41px !important;
    right: 575px !important;
    border-radius: 5px;
    border: 0px solid grey;
}

#clearAnnouncementsText {
    margin-top: 5px;
}

#clearAnnouncementsText:hover {
    cursor: pointer;
}



