.login-grid {
    background: -webkit-gradient(linear, left top, right bottom, from(whitesmoke), to(#EEEEEE));
    background: -webkit-linear-gradient(top left, whitesmoke, #EEEEEE);
    background: -o-linear-gradient(top left, whitesmoke, #EEEEEE);
    background: linear-gradient(to bottom right, whitesmoke, #EEEEEE);
    /* background-color: #EEEEEE; */
    width: 100%;
    height: 100%;
}

.container {
    display: -ms-flexbox;
    display: flex;
  height: 100%;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.amora {
    text-shadow: 2px 4px #ff00;
    margin: 0px;
    width: 240px;
    height: auto;
}

.subtitle {
    font-size: 16px;
    color: #4A4A4A;
    text-shadow: 1px 4px #ff00;
    margin: 8px;
    font-weight: bolder;
}

img {
  padding: 0px;
}

.googleLogin {
    width: 170px;

}

.googleLogin:hover {
    margin-top: -1px;
    cursor: pointer;
}

.signin-button {
    border-radius: 4px;
    background: rgb(233, 233, 233);
    color: rgb(85, 85, 85);
    font-size: 14px;
    font-weight: 100;
    padding: 8px;
    margin: 8px;
}

.login-row {
  margin-top: 20%;
}



